html, body, #root {
    height: 100%;
}
body {
    margin: 0;
}
.flex-container {
    height: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.flex-container.alarm {
    background-color: red;
}

.row {
    width: auto;
}
.flex-container.alarm .flex-item{
    color: white;
}
.flex-item {
    padding: 5px;
    margin: 10px;
    line-height: 20px;
    text-align: center;
    font-weight: bold;
    font-size: 10vw;
}